:root {
  --charcoal-grey: #464052;
  --black: #000;
  --cool-grey: #a4a6b0;
  --battleship-grey: #74747e;
  --pale-lilac: #e6e6eb;
  --charcoal-grey-two: #363a42;
  --maincolor: #5b36ac;
}

.container {
  width: 23.438rem;
  height: 41.688rem;
  border-bottom: 1px solid grey;
  background-color: #fff;
}

.header1 {
  height: 2rem;
  background-color: var(--maincolor);
}

.header2 {
  height: 2rem;
  background-color: var(--maincolor);
}

.header2_menu {
  width: 10%;
  float: left;
  margin-left: 1rem;
  background-color: var(--maincolor);
}

.header2_title {
  width: 70%;
  float: left;
  text-align: center;
  background-color: var(--maincolor);
  font-family: AppleSDGothicNeo;
  font-size: 1rem;
  font-weight: bold;
  text-align: center;
  color: #fff;
}

.header2_person {
  float: right;
  margin-right: 1rem;
  background-color: var(--maincolor);
}

.middle {
  height: 3.5rem;
  margin: 0.625rem 0 0;
  padding: 0.5rem 0.7rem;
  background-color: #fff;
}

.middle-left {
  width: 15%;
  height: 100%;
  height: 3.5rem;
  border-radius: 70%;
  overflow: hidden;
  float: left;
}

.middle-mid {
  margin-top: 0.4rem;
  margin-left: 1%;
  float: left;
  padding-left: 0.5rem;
  width: 64%;
  /* background-color: rgb(59, 38, 38); */
}

.middle-right {
  width: 17%;
  height: 100%;
  float: left;
  /* background-color: rgb(156, 133, 133); */
}

.profile {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile-name {
  width: 100%;
  margin: 0 9rem 0.188rem 0;
  font-size: 0.9rem;
  font-weight: bold;
  color: var(--charcoal-grey);
}

.profile-content {
  width: 100%;
  margin: 0.188rem 0 0;
  font-size: 0.7rem;
  font-weight: 500;
  letter-spacing: -0.1px;
  color: var(--cool-grey);
}

.times {
  margin: 0.75rem 0 0.375rem 0.813rem;
  opacity: 0.4;
  font-size: 0.688rem;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: right;
  color: var(--charcoal-grey-two);
}

.remain {
  width: 1.125rem;
  height: 1.125rem;
  margin: 0 0 0 2.5rem;
  padding-bottom: 0.3rem;
  border-radius: 70%;
  background-color: var(--maincolor);
  text-align: center;
}

span {
  font-family: AppleSDGothicNeo;
  font-size: 0.625rem;
  font-weight: bold;
  color: #fff;
  margin-bottom: 1rem;
}