  text-align: center;
} */

:root {
    --charcoal-grey: #464052;
    --black: #000;
    --cool-grey: #a4a6b0;
    --battleship-grey: #74747e;
    --pale-lilac: #e6e6eb;
    --charcoal-grey-two: #363a42;
    --maincolor: #5b36ac;
}

.room_container {
    width: 23.438rem;
    height: 41.688rem;
    background-color: #f9f9fb;
    border-bottom: 1px solid grey;
}

.room_header1 {
    height: 2rem;
    background-color: var(--maincolor);
}

.room_header2 {
    height: 2rem;
    background-color: var(--maincolor);
}

.room_header2_menu {
    width: 10%;
    float: left;
    margin-left: 1rem;
    background-color: var(--maincolor);
}

.room_header2_title {
    width: 60%;
    float: left;
    text-align: center;
    background-color: var(--maincolor);
    font-family: AppleSDGothicNeo;
    font-size: 1rem;
    font-weight: bold;
    text-align: center;
    color: #fff;
}

.room_header2_person {
    float: right;
    margin-right: 1rem;
    background-color: var(--maincolor);
}

.imgs {
    margin-left: 1rem;
}

.room_middle {
    height: 85%;
    margin: 0.625rem 0 0;
    padding: 0.5rem 0.7rem;
    background-color: #f9f9fb;
}

.msg-div {
    height: 90%;
}

.input-div {
    height: 10%;
}

.input_left {
    float: left;
}

.input_right {
    float: right;
}

.Recipient_user {
    width: 60%;
    height: 2.8rem;
    border-radius: 20px;
    margin: 0.5rem;
    padding-left: 1rem;
    text-align: left;
    line-height: 45px;
    box-shadow: 0 2px 4px 0 var(--black-10);
    background-color: #fff;
    white-space: nowrap;
    overflow: hidden;
    font-size: 0.875rem;
    font-weight: 600;
    color: var(--charcoal-grey-two);
    border-bottom: 1px solid grey;
    float: left;
}

.times1 {
    padding-top: 1.5rem;
    opacity: 0.4;
    font-size: 0.688rem;
    font-weight: 500;
    text-align: right;
    color: var(--charcoal-grey-two);
    float: left;
}

.hr-sect {
    display: flex;
    flex-basis: 100%;
    align-items: center;
    color: rgba(0, 0, 0, 0.35);
    font-size: 12px;
    margin: 8px 0px;
}

.hr-sect::before,
.hr-sect::after {
    content: "";
    flex-grow: 1;
    background: rgba(0, 0, 0, 0.35);
    height: 1px;
    font-size: 0px;
    line-height: 0px;
    margin: 0px 16px;
}

.times2 {
    padding-top: 1.5rem;
    opacity: 0.4;
    font-size: 0.688rem;
    font-weight: 500;
    text-align: right;
    color: var(--charcoal-grey-two);
    float: right;
}

.clear {
    clear: both;
}

.send_user {
    width: 60%;
    height: 2.8rem;
    border-radius: 20px;
    margin: 0.5rem;
    padding-left: 1rem;
    text-align: left;
    line-height: 45px;
    box-shadow: 0 2px 4px 0 var(--black-10);
    background-color: var(--maincolor);
    white-space: nowrap;
    overflow: hidden;
    font-size: 0.875rem;
    font-weight: 600;
    color: #fff;
    border-bottom: 1px solid grey;
    float: right;
}

textarea {
    width: 17.563rem;
    height: 3.125rem;
    border-radius: 25px;
    padding-left: 1rem;
    text-align: left;
    line-height: 50px;
    color: #7f7f83;
    box-shadow: 0 2px 4px 0 var(--black-10);
    background-color: #fff;
    font-weight: bold;
}

.imgs2 {
    margin-left: -0.22rem;
}

.sendmsg {
    width: 1.1rem;
    height: 1rem;
    padding: 1rem 0.75rem;
    border-radius: 50%;
    background-color: #5B36AC;
}

.ani-navbar:hover {
    transform: translateX(0px);
    text-align: center;
}

.ani-navbar {
    width: 100px;
    height: 10%;
    background: white;
    height: 100%;
    padding: 25px;
    position: fixed;
    z-index: 5;
    transition: all 1s;
    transform: translateX(-100px);
}

.test_obj {
    position: relative;
    animation: fadeInDown 1s;
}